<script>
import BookCard from "@/components/book/BookCard.vue";
import userBookService from "@/API/services/user-books.service";
import { watchEffect } from "vue";
import helper from "@/utilities/helper";

export default {
    name: "Free Book",
    components: { BookCard },
    props: ["page"],
    created() {
        watchEffect(() => {
            this.getBooks(this.page);
        });
    },

    data() {
